import Select from 'react-select';
import { useState } from 'react';
import Icon from '@mui/material/Icon';
import SendIcon from "@mui/icons-material/Send";
import Stack from "@mui/material/Stack";
import { useInterval } from "../hooks/use-interval";
import {ChatBubble} from './chatBubble';
import { FormControl, TextField, Button, Card, MenuItem, CardContent } from '@mui/material';
import "./chat.css";

export const Chat = () => { 

    const [chats, setChats] = useState([{id: '', name:''}]);
    const [messages, setMessages] = useState([{chatId: "", text: "hi", username: "shay", isoDate: "", id: ""}, {
        text: "wsp?", username:"M"}, {text:'nm', username: 'shay' }]);
    const [chatId, setChatId] = useState('b23ae22b-3d26-461d-a11d-08c5aa19bd3c');
    const[newChat, setNewChat] = useState('');
    const[user, setUser] = useState('');
    const [newMsg, setNewMsg] = useState('');
   
    useInterval(
        () => {    
            chats = fetch("https://z36h06gqg7.execute-api.us-east-1.amazonaws.com/chats")
            .then((response) => response.json())
            .then((data) => {setChats(data.Items);
            console.log(chatId);
            });
        },
        4000
    );
    
    useInterval(
        (params) => {
        const chatId = params[0]; 
           fetch(`https://z36h06gqg7.execute-api.us-east-1.amazonaws.com/chats/${chatId}/messages`)
             .then((response) => response.json())
             .then((data) => {
               setMessages(data.Items);
             }
            );
         },
          4000,
          chatId
       );

    const handleChatChange = selectedChat => {

        setChatId(selectedChat.id);
        console.log("chat id: " + selectedChat.name);
    }

    function addMessage () {
        const msg = {text: newMsg, username: user};

        fetch(`https://z36h06gqg7.execute-api.us-east-1.amazonaws.com/chats/${chatId}/messages`, {
            method: "PUT",
            headers: {
             "Content-Type": "application/json", // tells REST that we will send the body data in JSON format
            },
            body: JSON.stringify(msg),
        });
    }

    function addChat(){
        console.log("add chat: " + newChat);
        
        fetch('https://z36h06gqg7.execute-api.us-east-1.amazonaws.com/chats', {
            method: "PUT",
            headers: {
             "Content-Type": "application/json", // tells REST that we will send the body data in JSON format
            },
            body: JSON.stringify(newChat),
        });
    }

    return(
        <div className="chatRoom">
            <link
             rel="stylesheet"
             href="https://fonts.googleapis.com/icon?family=Material+Icons"
            />
            <h1>Chat Room</h1>
            <Stack direction="row" spacing={1}>
            <ChatList chats={chats} handleChatChange = {handleChatChange}/>  
                <TextField id="outlined-basic" variant="outlined" size= "small"
                 label = "Enter Username"
                onChange={(event) => setUser(event.target.value)}
                />
                <TextField id="outlined-basic" variant="outlined" size= "small"
                 label = "Enter New Chat"
                onChange={(event) => setNewChat(event.target.value)}
                />
                <Button onClick={addChat}>
                <Icon fontSize= 'large' >add_circle</Icon></Button>
            </Stack>
            <div className="message">
                <Message messages={messages} user={user} />
            </div>
                <Stack direction="row" spacing={2} id= 'addMsg'>
                <TextField id="outlined-basic" variant="outlined" size= "small"
                 placeholder = "Enter Message"
                onChange={(event) => setNewMsg(event.target.value)}
                />
                <Button variant="contained" onClick={addMessage} endIcon={<SendIcon />}>Send</Button>
                </Stack>
         
        </div>
    );
}
const Message = (props) => {

    const chats = props.messages.map(message => {

            if(message.username !== props.user){
                return{ type : 0, text: message.text, image: "https://upload.wikimedia.org/wikipedia/commons/7/7c/Profile_avatar_placeholder_large.png" }
            }
            else { 
                return{ type : 1, text: message.text, image: "https://upload.wikimedia.org/wikipedia/commons/7/7c/Profile_avatar_placeholder_large.png"}   
            }
    }); 
        

    return(
        <div>
         {<ChatBubble messages = {chats} />}
        {/*props.messages.map(message => {
            if(message.username !== props.user)
               return (
               <Card id='msg' sx={{textAlign: 'left'}}><CardContent>{message.text}</CardContent></Card>)
            
            else
             return (<Card id='msg' sx={{textAlign: 'right'}}><CardContent>{message.text}</CardContent></Card>);
         }
        )*/}
        </div>
    );
   
}


const ChatList = (props) => {

    const tempChats = props.chats.map(chat => {
        return {value: chat.id, label: chat.name}
    });
    
    return(
      <FormControl>
      <Select
      placeholder={"Select Chat Room"} 
      onChange ={props.handleChatChange}
      options ={props.chats}
      >
        {/*props.chats.map(chat => 
            <MenuItem value={chat.id}>{chat.name}</MenuItem>
        )
        */}

    </Select>
    </FormControl>
        
    );

}

